.featured {
    flex: 2;
    padding: 20px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47);
    box-shadow: 2px 4px 10px 1px rgba(201,201,201,0.47);
    border-radius: 10px;

    .top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: gray;
    }

    .title {
        font-size: 16px;
        font-weight: 500;
    }

    .bottom {
        padding: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 15px;
    }

    .featured-chart {
        height: 100px;
        width: 100px;
    }

    .title {
        font-weight: 500;
        color: gray;
    }

    .desc {
        font-weight: 300;
        font-size: 12px;
        color: gray;
    }

    .summary {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .item {
            text-align: center;

            .itemTitle {
                font-size: 14px;
                color: gray;
            }

            .itemResult {
                display: flex;
                align-items: center;
                margin-top: 10px;
                font-size: 14px;

                &.positive {
                    color: green;
                }
                
                &.negative {
                    color: red;
                }
            }
        }
    }
}