.navbar {
    height: 50px;
    border-bottom: 0.5px solid lightgray;
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #555;

    .wrapper {
        padding: 20px;
        display: flex;
        justify-content: space-between;
        width: 100%;

        .search {
            display: flex;
            align-items: center;
            border: 0.5px solid lightgray;
            padding: 3px;
        }

        input {
            border: none;
            outline: none;
            background-color: transparent;

            &::placeholder {
                font-size: 12px;
            }
        }

        .items {
            display: flex;

            .icon {
                cursor: pointer;
            }

            .item {
                display: flex;
                align-items: center;
                margin-right: 20px;
                position: relative;
            }

            .avatar {
                height: 30px;
                width: 30px;
                border-radius: 50%;
            }

            .counter {
                background-color: red;
                height: 15px;
                width: 15px;
                border-radius: 50%;
                color: white;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 10px;
                font-weight: bold;
                position: absolute;
                top:-5px;
                right: -5px;
            }
        }
    }
}   